* {
    box-sizing: border-box;
}

.sendForm {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 44px;
    width: 90vw;
    height: 10vh;
    align-items: center;

}

.textarea {
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);
    line-height: 17px;
    background-color: #DBDFE9;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;

    resize: none;
    width: 70%;
    min-height: 30%;
    border: none;
    outline: none;

    padding: 12px 12px;

}


.button {
    color: #FFFFFF;
    border: none;
    background: #0066CC;
    border-radius: 20px;
    width: 120px;
    height: 40px;
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}

.button:hover {
    /*box-shadow: 0 0 10px 0 #0066CC inset, 0 0 20px 2px #0066CC;*/
    /*border: 3px solid #0066CC;*/
    box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;

}
.textarea :active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}


.button:active {
    background-color: whitesmoke;
    color: #3498db;
}
